

.Slider_container {
    max-width: 100%;
    background-color: white !important;
    padding-left: 85px;
    padding-right: 115px;
    font-family: 'Poppins', sans-serif;
}

.car{
    padding-left: 108px;
    padding-right: 0rem;
}
.heading{
    color: #8108AB; 
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 35px;
    margin-bottom: 40px;
  }

.react-multi-carousel-item {
    transform-style: preserve-3d;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    margin-right: 44px;
}
.react-multi-carousel-track{
    width: 103%;
}

.Slider__photo{
    position: relative;
    max-width: 100%;
 }
 /* .Slider__measurement{
     margin-right: 150px;
 }
 .Slider__row{
     margin-left: 13rem; 
 } */
 .Slider__picture{
     width: 240px;
     height: 325px;
     border-radius: 20px;
 }
 .Slider__names{
    position: absolute;
    bottom:-1rem;
    text-align:left;
    color: #fff;
    width: 240px;
    background-color: black;
    opacity: .7;
    font-family: 'Poppins', sans-serif;
    height: 30px;
    font-size: 20px;
    font-weight: 520;
    padding-left: 15px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
 }
 .Slider__fname{
     margin-left: 10px;
     margin-top: 12px;
     font-family: 'Poppins', sans-serif;
     font-weight: 700;
     font-size: 20px;
     line-height:1.7rem;
     color:#2541B2;
     width: 240px;
 }
 .Slider__address{
     margin-left: 10px;
     font-family: 'Poppins', sans-serif;
     font-weight:400;
     font-size: 15px;
     line-height: 1.5rem;
     width: 240px;
     margin-top: 10px;
 }
 .Slider__phone{
     margin-left: 10px;
     font-family: 'Poppins',sans-serif;
     font-weight: 400;
     font-size: 15px;
     margin-top: 10px;
     margin-bottom: 2rem;
     width: 240px;
 }



